.hamburger{position:absolute;top:0;left:0;padding:10px;margin:16px;display:inline-block;border:none;background-color:transparent;z-index:2;cursor:pointer}.hamburger__box{width:40px;height:24px;display:inline-block;position:relative}.hamburger__inner{width:100%;height:4px;position:absolute;background-color:#000;left:0;top:50%;transition:0.3s background-color ease-in-out}.hamburger__inner::after{content:'';width:100%;height:4px;position:absolute;background-color:#000;left:0;top:10px;transition:0.3s transform ease-in-out}.hamburger__inner::before{content:'';width:100%;height:4px;position:absolute;background-color:#000;left:0;top:-10px;transition:0.3s transform ease-in-out}.hamburger__active .hamburger__inner{background-color:transparent}.hamburger__active .hamburger__inner::after{transform:translateY(-10px) rotate(45deg)}.hamburger__active .hamburger__inner::before{transform:translateY(10px) rotate(-45deg)}.logo{position:absolute;right:16px;top:16px}.navigation{position:absolute;top:0;left:-1500px;width:230px;height:100%;background:linear-gradient(348.04deg, rgba(255,255,255,0.42) 0%, rgba(255,255,255,0) 102.14%);-webkit-backdrop-filter:blur(7px);backdrop-filter:blur(7px);z-index:1;transition:0.3s all ease-in-out;display:flex;flex-direction:column;visibility:hidden}.navigation__active{left:0;visibility:visible}.navigation__list{margin:100px 16px;padding:0;list-style:none;font-size:24px}.navigation__list--item{color:#000;text-align:center;background:#E3FFFA;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:0px 20px;margin:16px 0;padding:12px;transition:0.3s background-color ease-in-out}.navigation__list--item:hover{background-color:rgba(158,255,234,0.61)}.navigation__link:focus,.navigation__link:hover,.navigation__link:visited,.navigation__link:link,.navigation__link:active{text-decoration:none}.clockSection{height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.clock{font-size:72px;font-weight:400;margin:0}.date{font-size:18px;font-weight:400}.list{display:flex;flex-direction:column;align-items:center}.list__header{font-size:48px;font-weight:400;margin:62px 0 24px 0}.form__input{padding:12px 12px;font-size:16px;font-weight:400;color:#3a3a3a;border-radius:24px 0px 0px 0px;border:none;outline:none;background:#FFFFFF;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:47px 0px 0px 0px;transition:0.3s all ease-in-out}.form__input:focus{background-color:#f0f0f0}.form__input:hover{background-color:#f0f0f0}@media (min-width: 550px){.form__input{padding:12px 32px}}.form__button{padding:12px 12px;font-size:16px;font-weight:400;color:#3a3a3a;border-radius:0px 0px 24px 0px;outline:none;border:none;background:#FFFFFF;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:0px 20px 20px 0px;transition:0.3s all ease-in-out;cursor:pointer}.form__button:active{background-color:#f0f0f0;transform:translateY(1px)}.form__button:hover{background-color:#f0f0f0}@media (min-width: 550px){.form__button{padding:12px 32px}}.list{padding:0}.list__item{display:flex;justify-content:space-between;align-items:center;width:300px;margin:8px 0;background:linear-gradient(98.49deg, rgba(255,255,255,0.608) 0%, rgba(196,196,196,0) 180.61%);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);padding:6px;border-radius:6px}@media (min-width: 550px){.list__item{width:350px}}.list__item--text{overflow:auto;word-wrap:break-word}.list__item--active{background:linear-gradient(98.49deg, rgba(0,255,21,0.608) 0%, rgba(45,119,16,0) 180.61%)}.buttons__box{display:flex}.button__confirm{margin-right:8px;width:30px;height:25px}.button__delete{margin-right:8px;font-weight:600;width:30px;height:25px}html{scroll-behavior:smooth}*,*::after *::before{box-sizing:border-box;padding:0;margin:0}body{font-family:'Montserrat', sans-serif;padding:0;margin:0}

